import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import * as moment from 'moment';
@Component({
  selector: 'ibss-common-about2',
  templateUrl: './about2.component.html',
  styleUrls: ['./about2.component.scss']
})
export class About2Component implements OnChanges {
  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() isNew: boolean = true;
  @Input() isView: boolean = false;
  @Input() aboutContent: any =  {};
  @Input() index: any;
  @Output() onNav = new EventEmitter();
  content: any;
  constructor() { }


  ngOnChanges() {
    this.content = JSON.parse(JSON.stringify(this.aboutContent));
  }

  getFormattedDate(date:any) {
    return moment(date).format('Do MMMM YYYY');
  }

  getFormattedTime(time:any) {
    return moment(time).format('hh:mm a')
  }
}
