import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { Lightbox } from 'ngx-lightbox';
declare var $: any;

@Component({
  selector: 'ibss-common-gallery2',
  templateUrl: './gallery2.component.html',
  styleUrls: ['./gallery2.component.scss']
})
export class Gallery2Component implements OnInit, OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() galleryContent: any =  {};
  @Input() isView: boolean = false;
  @Output() onNav = new EventEmitter();
  content: any;
  selectedGroup:any ='';
  selectedImage:any = null;
  constructor(
    private _lightbox: Lightbox
  ) {

   }

  ngOnInit() {
  }

  ngOnChanges() {
    this.content = JSON.parse(JSON.stringify(this.galleryContent));
  }

  onViewImage(image: any) {
    this.selectedImage = image;
    const album:any = [
      {
        src:image
      }
    ]
    this._lightbox.open(album, 0)
  }
}
