export interface IBSSMAINITEM {
    type: string;
    title: string,
    id: string,
    layoutID: string,
    templateID: string,
    pages: Array<IBSSPAGE>,
    currentStep: number,
    subStep: number,
    status: string
}

export interface IBSSPAGE {
    id: string,
    title: string,
    sections: Array<IBSSSECTION>,
    template: any,
    pages: any
}

export interface IBSSSECTION {
    title: string,
    components: Array<IBSSCOMPONENT>
}

export interface IBSSCOMPONENT {
    componentID: string,
    conent: any
}

