import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { WIZARD_CONFIG } from 'src/app/wizard/wizard.config';
import { WizardService } from 'src/app/wizard/wizard.service';
declare var $: any;
@Component({
  selector: 'ibss-common-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FooterComponent implements OnChanges  {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputPayload: any;
  @Input() index: any;
  @Input() menuId: any;
  @Input() isView: boolean = false;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  content: any;
  editItem :any;
  config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  constructor(private ws: WizardService) { }

  ngOnChanges() :void {
    let content = this.config.footer1Content;
    if (this.inputPayload) {
      content = this.inputPayload;
    }
    this.content = JSON.parse(JSON.stringify(content));
    this.setEditData();
  }

  onCancel() {
    this.setEditData();
  }

  setEditData() {
    this.editItem = JSON.parse(JSON.stringify(this.content));
    this.editItem.feature = "";
  }

  onDelete() {

  }

  onDone() {
    this.content = JSON.parse(JSON.stringify(this.editItem));
    this.onAction.emit({index:this.index, component:{ id: "footerComponent", template:JSON.parse(JSON.stringify(this.content))}});
  }

  onNavigate(id: any){
    console.log('onNvigate', id);
    this.onNav.emit(id);
  }

  onAddLink() {

    let temp = {
      font:"fa fa-link",
      title: "link",
      link:"https://domian.com"
    }
    this.editItem.links.push(temp);
  }

  onDeleteLink(index: any) {
    this.editItem.links.splice(index, 1);
  }

  onModify(index: any) {
    this.ws.modifyIndex = index;
  }
  
  OpenEditModal(index: any) {
    $('#editFooterModal_'+index).modal('show');
  }

  onAddLinks() {
    
  }

  onRemoveLinks(index: any) {

  }
}
