import { Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';
import * as moment  from 'moment';
@Component({
  selector: 'ibss-common-header2',
  templateUrl: './header2.component.html',
  styleUrls: ['./header2.component.scss']
})
export class Header2Component implements OnChanges {
  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() headerContent: any;
  @Input() index: any;
  @Input() isView: boolean = false;
  @Output() onNav = new EventEmitter();
  editHeader :any;
  formattedDate: any = "";
  constructor() {
    
   }

   ngOnChanges() {
    this.formattedDate = moment(this.headerContent.marriageDate).format('Do MMMM YYYY');
    this.editHeader = JSON.parse(JSON.stringify(this.headerContent));
    this.editHeader.pages.map((value: any, indx: any) => {
      value.isEdit=false;
      value.hasSupPages= (value.subPages.length>0)
    });
  }


  setId(page: any) {
    if (page.title && page.title.trim().length>0) {
      page.id = page.title.replace(/ /g,"_")
    }
  }

  onNavigate(id: any){
    this.onNav.emit(id);
  }

}
