import { Component, EventEmitter, Input, NgZone, OnChanges, Output, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'ibss-common-header1',
  templateUrl: './header1.component.html',
  styleUrls: ['./header1.component.scss']
})
export class Header1Component implements OnChanges {
  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() headerContent: any;
  @Input() index: any;
  @Input() isView: boolean = false;
  @Output() onNav = new EventEmitter();
  editHeader :any;
  
  constructor(private ngZone:NgZone) { }

  ngOnChanges() {
    this.editHeader = JSON.parse(JSON.stringify(this.headerContent));
    this.editHeader.pages.map((value: any, indx: any) => {
      value.isEdit=false;
      value.hasSupPages= (value.subPages.length>0)
    });
  }


  setId(page: any) {
    if (page.title && page.title.trim().length>0) {
      page.id = page.title.replace(/ /g,"_")
    }
  }

  onNavigate(id: any){
    this.onNav.emit(id);
  }
 
}
