import { Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';
import { DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'ibss-youtube-videos',
  templateUrl: './youtube-videos.component.html',
  styleUrls: ['./youtube-videos.component.scss']
})
export class YoutubeVideosComponent implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() content: any;
  @Input() index: any;
  @Input() isView = false;
  @Input() menuId: any;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  videoId = "JddGlT64N2s";
  youtubeUrl = "https://www.youtube-nocookie.com/embed/";
  videoSrc:any = "";;
  constructor(private sanitizer: DomSanitizer) { 
  }
  
  ngOnChanges() {
    console.log('YoutubeVideosComponent',this.content);
    this.videoSrc = this.youtubeUrl+this.content.src;
    console.log("videoSrc", this.videoSrc);
    
  }

  onEdit() {
    const params:any = {
      action:"edit",
      component:this.content,
      index:this.index
    }

    this.onAction.emit(params);
  }

}
