import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';

@Component({
  selector: 'ibss-common-footer2',
  templateUrl: './footer2.component.html',
  styleUrls: ['./footer2.component.scss']
})
export class Footer2Component implements OnInit, OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() footerContent: any =  {};
  @Input() isView: boolean = false;
  @Output() onNav = new EventEmitter();
  content: any;
  constructor() { }

  ngOnInit() { }
  ngOnChanges() :void{
    this.content = JSON.parse(JSON.stringify(this.footerContent));
  }

  saveChanges(value:any) {
    this.content.copyrightText = value
  }

}
