import { Component, EventEmitter, Input, NgZone, OnChanges, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { OwlOptions } from 'ngx-owl-carousel-o/lib/models/owl-options.model';
import { WIZARD_CONFIG } from 'src/app/wizard/wizard.config';
import { WizardService } from 'src/app/wizard/wizard.service';
declare var $:any;
@Component({
  selector: 'ibss-common-introduction',
  templateUrl: './introduction.component.html',
  styleUrls: ['./introduction.component.scss']
})
export class IntroductionComponent implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputPayload: any;
  @Input() index: any;
  @Input() isView: boolean= false;
  @Input() menuId: any;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();

  introComponents = [
    {
      id:"IBSSCOMMONINTRO1",
      title:"Intro Layout 1"
    },
    {
      id:"IBSSCOMMONINTRO2",
      title:"Intro Layout 2"
    }

  ]

  customOptions: OwlOptions = {
    loop: true,
    mouseDrag: false,
    touchDrag: false,
    pullDrag: false,
    dots: false,
    navSpeed: 700,
    navText: ['', ''],
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 2
      },
      740: {
        items: 3
      },
      940: {
        items: 4
      }
    },
    nav: true
  }

  intro1Items = [];
  editItems:any = [];
  editSlide:any = {
    selected: "",
    image: null,
    hasCaption:false,
    captions: {
      heading: "",
      content: ""
    }
  }

  content :any;

  config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  constructor(private ws: WizardService, private ngZone: NgZone) { }

  ngOnChanges() {

    let content = this.config.intro1Items;
    if (this.inputPayload) {
      content = this.inputPayload;
    } 

    this.intro1Items = JSON.parse(JSON.stringify(content));

    this.setEditData();
  }

  onCancel() {
    this.setEditData();
  }

  setEditData() {
    this.editItems = JSON.parse(JSON.stringify(this.intro1Items))
    this.content = JSON.parse(JSON.stringify(this.intro1Items))
  }
  onAddSlide() {
    const temp:any = {
      selected: "",
      image: "../../../../assets/images/web/layout1/slide-01.jpg",
      hasCaption:true,
      captions: {
        heading: "Slide Label",
        content: "Nulla vitae elit libero, a pharetra augue mollis interdum."
      }
    }
    this.ngZone.run(()=>{
      this.editItems.push(temp);
      this.editSlide.selected=(this.editItems.length-1).toString();
      this.onSelectSlide();
    });

  }

  OpenEditModal(index:any) {
    $('#editIntroModal_'+index).modal('show');
  }

  onSelectSlide() {

    this.editSlide.image = null,
    this.editSlide.hasCaption = false,
    this.editSlide.captions = {
      heading: "",
      content: ""
    }

    if (this. editSlide.selected && this. editSlide.selected!= "") { 
      const d = JSON.parse(JSON.stringify(this.editItems[Number(this.editSlide.selected)]));
      this.editSlide.image = d.image;
      this.editSlide.hasCaption = d.hasCaption;
      this.editSlide.captions = d.captions;
    }
  }

  readURL(event: any): void {
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = e => {
        // this.editItems[Number(this.editSlide.selected)].image = reader.result
        this.editSlide.image = reader.result;
      };
      reader.readAsDataURL(file);
    }
  }

  onUpdate() {
    this.ngZone.run(()=>{
      if (this.editSlide.selected && this.editSlide.selected!="") {
        this.editItems[Number(this.editSlide.selected)].image = this.editSlide.image;
        this.editItems[Number(this.editSlide.selected)].hasCaption = this.editSlide.hasCaption;
        this.editItems[Number(this.editSlide.selected)].captions = this.editSlide.captions;
      } else {
        this.editItems.push({
          image :this.editSlide.image,
          hasCaption: this.editSlide.hasCaption,
          captions: this.editSlide.captions
        });
      }
    });
  }

  onDelete() {
    this.editItems.splice(Number(this.editSlide.selected),1);
    this.editSlide.selected = "";
  }

  onDone() {
    this.intro1Items = JSON.parse(JSON.stringify(this.editItems));
    this.content.content = JSON.parse(JSON.stringify(this.intro1Items))
    this.onAction.emit({index:this.index, component:{ id: "introComponent", template:JSON.parse(JSON.stringify(this.content))}});

  }

  onNavigate(id: any){
    console.log('onNvigate', id);
    this.onNav.emit(id);
  }

  onModify(index: any) {
    this.ws.modifyIndex = index;
  }

  onEdit() {
    const params:any = {
      action:"edit",
      component:this.content,
      index:this.index
    }

    this.onAction.emit(params);
  }
}
