import { Component, Input, OnChanges } from '@angular/core';

@Component({
  selector: 'ibss-common-introduction2',
  templateUrl: './introduction2.component.html',
  styleUrls: ['./introduction2.component.scss']
})
export class Introduction2Component implements OnChanges {
  showEdit = false;
  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() introContent: boolean = false;
  @Input() index: any;
  @Input() isView: boolean = false;
  content: any = [];
  constructor() { }

  ngOnChanges() {
    console.log('this.introContent', this.introContent);
    this.content = JSON.parse(JSON.stringify(this.introContent));
  }
}
