import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';

@Component({
  selector: 'ibss-common-about1',
  templateUrl: './about1.component.html',
  styleUrls: ['./about1.component.scss']
})
export class About1Component implements OnInit, OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() isNew: boolean = true;
  @Input() isView: boolean = false;
  @Input() aboutContent: any =  {};
  @Input() index: any;
  @Output() onNav = new EventEmitter();
  content: any;
  constructor() { }

  ngOnInit() { }
  ngOnChanges() {
    this.content = JSON.parse(JSON.stringify(this.aboutContent));
  }

  onFocusOut(field:any, content:any) {
    console.log(field, content);
  }
} 
