import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { WIZARD_CONFIG } from 'src/app/wizard/wizard.config';
import { WizardService } from 'src/app/wizard/wizard.service';
declare var $: any;

@Component({
  selector: 'ibss-common-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.scss']
})
export class BannerComponent implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputPayload: any;
  @Input() index: any;
  @Input() isView = false;
  @Input() menuId: any;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  content: any;
  editItem :any;
  config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  
  constructor(private ws: WizardService) { }

  ngOnChanges() {
    let content = this.config.banner1Items;
    if (this.inputPayload) {
      content = this.inputPayload;
    }
    this.content = JSON.parse(JSON.stringify(content));
    console.log("BannerComponent",content);
    this.setEditData();
  
  }

  onCancel() {
    this.setEditData();
  }

  setEditData() {
    this.editItem = JSON.parse(JSON.stringify(this.content));
    this.editItem.feature = "";
  }

  onDelete() {

  }

  onDone() {
    this.content = JSON.parse(JSON.stringify(this.editItem));
    this.onAction.emit({index:this.index, component:{ id: "peoplesComponent", template:JSON.parse(JSON.stringify(this.content))}});
  }

  onNavigate(id: any){
    console.log('onNvigate', id);
    this.onNav.emit(id);
  }

  onAddFeature() {

    let temp = {
      icon:"fa-check-circle",
      title: JSON.parse(JSON.stringify(this.editItem.feature))
    }
    this.editItem.topfeatures.push(temp);
  }


  onDeleteFeature(index: any) {
    this.editItem.topfeatures.splice(index, 1);
  }

  onModify(index: any) {
    this.ws.modifyIndex = index;
  }

  OpenEditModal(index: any) {
    $('#editpeoplesModal_'+index).modal('show');
  }
  
  onEdit() {
    const params:any = {
      action:"edit",
      component:this.content,
      index:this.index
    }

    this.onAction.emit(params);
  }
}
