import { AfterViewChecked, ChangeDetectorRef, Component, ElementRef, EventEmitter, Input, OnChanges, OnInit, Output, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { ViewportScroller } from '@angular/common';
import { StyleService } from 'src/app/services/style.service';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'ibss-web-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss'],
  // encapsulation: ViewEncapsulation.None
})
export class WebLayoutComponent implements OnChanges, AfterViewChecked  {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() isLoading: boolean = false;
  @Input() isView: boolean = false;
  @Input() inputPayload: any;
  @Output() onAction = new EventEmitter();
  @ViewChild('pageInfo') pageInfo!: ElementRef; 
  @ViewChild('headerComponent') headerComponent!: TemplateRef<any>; 
  @ViewChild('introComponent') introComponent!: TemplateRef<any>; 
  @ViewChild('aboutComponent') aboutComponent!: TemplateRef<any>; 
  @ViewChild('galleryComponent') galleryComponent!: TemplateRef<any>; 
  @ViewChild('contactComponent') contactComponent!: TemplateRef<any>; 
  @ViewChild('footerComponent') footerComponent!: TemplateRef<any>; 
  @ViewChild('peoplesComponent') peoplesComponent!: TemplateRef<any>; 
  @ViewChild('bannerComponent') bannerComponent!: TemplateRef<any>; 
  @ViewChild('videoComponent') videoComponent!: TemplateRef<any>; 

  templateRefs: any;

  constructor(
    private viewportScroller: ViewportScroller,
    private cdref: ChangeDetectorRef,
    private styleService: StyleService,
    private titleService: Title
    ) { 

  }

  ngOnChanges() {
    console.log("WebLayout1Component", this.inputPayload);
    if (this.inputPayload) {
      if(!this.isDummy) {
        this.titleService.setTitle(this.inputPayload.title);
      }
      this.updateFontFamily();
    }
  }

  ngAfterViewChecked() {
    this.templateRefs = {
      headerComponent:this.headerComponent,
      introComponent:this.introComponent,
      aboutComponent:this.aboutComponent,
      galleryComponent:this.galleryComponent,
      contactComponent:this.contactComponent,
      footerComponent:this.footerComponent,
      peoplesComponent:this.peoplesComponent,
      bannerComponent:this.bannerComponent,
      videoComponent:this.videoComponent
    }
    this.cdref.detectChanges();
  }

  getTemplate(id: any) {
    console.log(this.templateRefs[id]);
    return this.templateRefs[id];
  }

  public onClick(elementId: any): void { 
    console.log('onClick',elementId);
      this.viewportScroller.scrollToAnchor(elementId);
  }
 
  public handleAction(evt: any) { 
    this.onAction.emit(evt)
  }

  updateFontFamily() {

    if (this.inputPayload && this.inputPayload.fonts) {
      this.inputPayload.fonts.forEach((font:any) => {
        const fontUri = `https://fonts.googleapis.com/css?family=${font.name.replace(' ', '+')}`;
        let pos = font.name.indexOf(':');
        if (pos > 0) {
          font.name = font.name.substring(0, pos);
        }

        const fId = font.name.replace(' ', '_')
        let link = document.getElementById(fId);
        if (!link) {
          link = document.createElement('link');
          link.id = fId;
          link.setAttribute('rel', 'stylesheet');
          link.setAttribute('href', fontUri);
          document.head.appendChild(link);
        }

        font.selector.forEach((ele:any) => {
          this.styleService.setStyle(ele, "font-family", font.name);
        });
      });
      // this.selectAll();
    }
  }

  // selectAll() {
  //   let textFontFamily = <HTMLInputElement>document.getElementById('ibss-weblayout');
  //   textFontFamily.select();
  // }
 
}
