import { AbstractType, Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';

@Component({
  selector: 'ibss-common-peoples1',
  templateUrl: './peoples1.component.html',
  styleUrls: ['./peoples1.component.scss']
})
export class Peoples1Component implements OnChanges {
  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() isNew: boolean = true;
  @Input() isView: boolean = false;
  @Input() peoples1Content: any =  {};
  @Input() index: any;
  @Output() onNav = new EventEmitter();
  content: any;
  constructor() { }

  ngOnChanges() {
    this.content = JSON.parse(JSON.stringify(this.peoples1Content));
  }

}
