import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { StyleService } from 'src/app/services/style.service';
import { WIZARD_CONFIG } from 'src/app/wizard/wizard.config';
import { WizardService } from 'src/app/wizard/wizard.service';
declare var $: any;
@Component({
  selector: 'ibss-common-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  encapsulation:ViewEncapsulation.None
})
export class HeaderComponent implements OnChanges {
  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputPayload: any;
  @Input() index: any;
  @Input() menuId: any;
  @Input() isView: boolean = false;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  headers = [
    {
      id:"IBSSCOMMONHEADER1",
      title:"Header Layout 1"
    },
    {
      id:"IBSSCOMMONHEADER2",
      title:"Header Layout 2"
    }

  ]

  content: any;

  editHeader :any;
  constructor(private ws:WizardService, private styleService:StyleService) { }

  ngOnChanges() {
    this.setEditData();
  }

  onCancel() {
    this.setEditData();
  }

  setEditData() {

    let content = this.config.header1Content;
    if (this.inputPayload) {
      content = this.inputPayload;
    }
    this.content = JSON.parse(JSON.stringify(content));
    console.log("HeaderComponent", this.content);

    if (this.isEdit) {
      const hNum = '.ibss-common-header'+this.content.id.replace('IBSSCOMMONHEADER','') + ' .'+this.content.class;
      this.styleService.setStyle(hNum,'position','relative' )
    }

    this.editHeader = JSON.parse(JSON.stringify(this.content));
    this.editHeader.pages.map((value: any, indx: any) => {
      value.isEdit=false;
      value.hasSupPages= (value.subPages.length>0)
    });
  }

  setId(page: any) {
    if (page.title && page.title.trim().length>0) {
      page.id = page.title.replace(/ /g,"_")
    }
  }

  onAdd(type: any,page: any){
    const name = (type === 'page') ? "Page" : "SubPage";
    let temp:any = {
      title:name,
      type:"section",
      id:name
    }

    if (type === 'page') { 
      temp.title = temp.title+"_"+(this.editHeader.pages.length+1);
      temp.subPages=[];
      temp.isEdit=false;
      temp.hasSupPages=false;
      this.editHeader.pages.push(temp);
    }
    else{
      temp.title = temp.title+"_"+ (page.subPages.length+1);
      page.subPages.push(temp);
    }
  }

  onDone() {
    this.content = JSON.parse(JSON.stringify(this.editHeader));
    this.onAction.emit({index:this.index, component:{ id: "headerComponent", template:JSON.parse(JSON.stringify(this.editHeader))}});
  }

  onDelete(index: any) {
    this.editHeader.pages.splice(index,1);
  }

  onNavigate(id: any){
    console.log('onNvigate', id);
    this.onNav.emit(id);
  }
 
  readURL(event: any): void {
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = e => {
        // this.editItems[Number(this.editSlide.selected)].image = reader.result
        this.editHeader.logoImage = reader.result
      };
      reader.readAsDataURL(file);
    }
  }

  onModify(index: any) {
    this.ws.modifyIndex = index;
  }

  OpenEditModal(index: any) {
    $('#editHeaderModal_'+index).modal('show');
  }
}
