import { Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';
import * as moment  from 'moment';

@Component({
  selector: 'ibss-common-banner1',
  templateUrl: './banner1.component.html',
  styleUrls: ['./banner1.component.scss']
})
export class Banner1Component implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() content: any;
  @Input() index: any;
  @Input() isView: boolean = false;
  @Output() onNav = new EventEmitter();
  formattedDate: any = "";
  constructor() {
    
   }

  ngOnChanges() {
    this.formattedDate = moment(this.content.date).format('Do MMMM YYYY');
  }

  setId(page: any) {
    if (page.title && page.title.trim().length>0) {
      page.id = page.title.replace(/ /g,"_")
    }
  }

  onNavigate(id: any){
    this.onNav.emit(id);
  }

}
