import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { API } from 'src/app/enums/api.enum';

@Component({
  selector: 'ibss-common-contact2',
  templateUrl: './contact2.component.html',
  styleUrls: ['./contact2.component.scss']
})
export class Contact2Component implements OnInit, OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() isView = false;
  @Input() contactContent: any =  {};
  @Input() profileName:any = "";
  @Output() onNav = new EventEmitter();
  config:typeof CONFIG = CONFIG;
  content: any;
  canSend = true;
  
  _saveUrl = this.config.getBaseURL(API.RESOURCES) + "/email/write";

  constructor(
    private toastr:ToastrService,
    public http: HttpClient
  ) { }

  ngOnInit() { }
  ngOnChanges() {
    this.content = JSON.parse(JSON.stringify(this.contactContent));
  }

  onSendMessage() {
    this.canSend = false;
    const isValid = this.validateFields();

    if (isValid) {
      console.log(this.content.fields);

      let ibssApp: any = localStorage.getItem("ibssApp");
      if (ibssApp) {
        ibssApp = JSON.parse(ibssApp);
      }
      let headers: HttpHeaders = new HttpHeaders();
      headers = headers.append('Accept', 'application/json');
      headers = headers.append('Content-Type', 'application/json');
      headers = headers.append("X-HTTP-Method", "POST");


      const params = {
        "serviceData": {
          "contactType": 1,
          "profileData": { "message": this.content.fields },
          "profileName": this.profileName
        }
      }
      this.http.post(this._saveUrl, params, { headers: headers })
      .subscribe((res: any) => {
        console.log('signup success', res);
        this.canSend = true;
        if (res.error && res.error.responseCode == 0) {
          this.toastr.success("Sent successfully");
          this.resetFields();
        } else {
          if (res.error) {
            if (res.error.businessValidation.length > 0) {
              for (let i = 0; i < res.error.businessValidation.length; i++) {
                this.toastr.error(res.error.businessValidation[i].errorDesc);
              }
            }

            if (res.error.dataValidation.length > 0) {
              for (let i = 0; i < res.error.dataValidation.length; i++) {
                this.toastr.error(res.error.dataValidation[i].errorDesc);
              }
            }
          } else if (res.responseCode == -1) {
            this.toastr.error("OOPS!!!, Please try again after sometimes");
          }

        }
      }, (error) => {
        this.canSend = true;
        this.toastr.error("OOPS!!!, Please try again after sometimes");
      })

    } else {
      this.canSend = true;
    }

  }

  resetFields() {
    this.content.fields.forEach((field:any) => {
      field.valid = true;
      field.validationMsg = "";
      field.value = "";
    });
  }

  validateFields() {
    let mandatoryFields = 0;
    let emptyFields = 0;
    let canSave = true;
    this.content.fields.forEach((field:any) => {
      field.valid = true;
      field.validationMsg = "";

      let validationMsg = "";
      let valid = true;
      if (field.required) {
        mandatoryFields++;
      } 
      if (field.value!=null &&  field.value!=undefined) {
          if (field.value=="") {
            validationMsg = "This Field is required";
            valid = false;
            emptyFields++;
          } else if (field.type == "email" && !this.config.regEx.email.test(field.type.toLowerCase())) {
              validationMsg = "Please enter a valid email address";
              valid = false;
          } else if (field.type == "number" && parseInt(field.type)<0) {
            validationMsg = "Please enter a valid data";
            valid = false;
          }
      } else {
        emptyFields++;
      }

      if (field.required) {
        field.validationMsg = validationMsg;
        field.valid = valid;
        canSave = valid;
      }
    });

    if(mandatoryFields === 0 && emptyFields > 1) {
      canSave=false;
    }
    
    return canSave;

  }
}
