import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { LightboxConfig } from 'ngx-lightbox';
import { ToastrService } from 'ngx-toastr';
import { WIZARD_CONFIG } from 'src/app/wizard/wizard.config';
import { WizardService } from 'src/app/wizard/wizard.service';
declare var $: any;
@Component({
  selector: 'ibss-common-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.scss']
})
export class GalleryComponent implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputPayload: any;
  @Input() index: any;
  @Input() menuId: any;
  @Input() isView: boolean = false;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  content: any;
  editItem: any;
  upload: any = {
    images: [],
    imageCaption: "",
    group: ""
  };
  selectedGroup:any = 'all';
  config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  constructor(public toastr: ToastrService,private ws: WizardService, private _lightboxConfig: LightboxConfig) {
    _lightboxConfig.showZoom = true;
    _lightboxConfig.showRotate = true;
    _lightboxConfig.centerVertically = true;
    _lightboxConfig.fitImageInViewPort = true;
   }

  ngOnChanges() {
    let content:any = this.config.gallery1Content;
    if (this.inputPayload) {
      content = this.inputPayload;
    }
    this.content = JSON.parse(JSON.stringify(content));


    this.setEditData();
  }

  onDone() {
    this.content = JSON.parse(JSON.stringify(this.editItem));
    this.selectedGroup = "all";
    this.onAction.emit({index:this.index, component:{ id: "galleryComponent", template:JSON.parse(JSON.stringify(this.content))}});
  }

  onCancel() {
    this.setEditData();
  }

  setEditData() {
    this.editItem = JSON.parse(JSON.stringify(this.content));
    this.upload.images = [];
    this.upload.group = "";
  }

  onAdd() {
    if (this.upload.group && this.upload.group.trim().length > 0 && this.upload.images.length > 0) {

      this.upload.images.forEach((image: any) => {
        let tempimage = {
          group: JSON.parse(JSON.stringify(this.upload.group)),
          data: image
        }
        this.editItem.images.push(tempimage);
      });

      let group = this.upload.group.trim().toLowerCase();
      let gIndx = this.editItem.groupsTitle.indexOf(group);
      if (gIndx == -1) {
        this.editItem.groupsTitle.push(group);
        this.editItem.groups.push({
          count: this.upload.images.length,
          title: JSON.parse(JSON.stringify(this.upload.group.trim()))
        });
      } else {
        this.editItem.groups[gIndx].count = this.editItem.groups[gIndx].count + this.upload.images.length;
      }

      this.upload.images = [];
      this.upload.imageCaption = "";
      this.upload.group = "";
    } else {
      let msg = "";
      if (!this.upload.group || this.upload.group.trim().length == 0) {
        msg = "Please enter the Group name";
      } else {
        msg = "Please select atleast 1 image";
      }

      this.toastr.error(msg);
    }
  }

  onNavigate(id: any) {
    console.log('onNvigate', id);
    this.onNav.emit(id);
  }


  onDeleteImage(index: any, image: any) {
    let group = image.group.toLowerCase();
    let gIndx = this.editItem.groupsTitle.indexOf(group);
    if (gIndx != -1) {
      this.editItem.images.splice(index, 1);
      this.editItem.groups[gIndx].count--;
      if (this.editItem.groups[gIndx].count == 0) {
        this.editItem.groups.splice(gIndx, 1);
        this.editItem.groupsTitle.splice(gIndx, 1);
      }
    }
  }

  readURL(event: any): void {
    if (event.target.files && event.target.files.length > 0) {
      for (let i = 0; i < event.target.files.length; i++) {
        const file = event.target.files[i];
        const reader = new FileReader();
        reader.onload = e => {
          this.upload.images.push(reader.result);
        };
        reader.readAsDataURL(file);
      }
    }
  }

  onModify(index: any) {
    this.ws.modifyIndex = index;
  }

  OpenEditModal(index: any) {
    $('#editGalleryModal_'+index).modal('show');
  }

  onEdit() {
    const params:any = {
      action:"edit",
      component:this.content,
      index:this.index
    }

    this.onAction.emit(params);
  }
}
